<template lang="html">
  <div class="container">
    <div class="row">
      <div class="col text-left">
        <div class="">
          <p></p>
          <h2>Simuladores Disponibles</h2>
          <p></p>
        </div>
        <br />
        <div class="col-md-12">
          <b-table striped hover :items="tutorias" :fields="fields">
            <template slot="cell(simulador)" slot-scope="data">
              <b-button
                size="m"
                variant="outline-primary"
                :to="{
                  name: 'EditTutoria',
                  params: { tutoriaId: data.item.id },
                }"
                >Iniciar</b-button
              ></template
            ></b-table
          >
        </div>
        <b-button :to="{ name: 'home' }" variant="primary">Regresar</b-button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import swal from "sweetalert";

export default {
  data() {
    return {
      fields: [
        { key: "nombre", label: "Nombre Simulador" },
        { key: "descripcion", label: "Descripción" },
        { key: "simulador", label: "Simulador" },
      ],
      tutorias: [],
    };
  },

  methods: {
    getSimuladores() {
      const path = "https://simuladoresbe.herokuapp.com/users/simuladores/";
      axios
        .get(path)
        .then((response) => {
          this.tutorias = response.data;
        })
        .catch((error) => {
          swal("", "", "error");
        });
    },
  },
  created() {
    this.getSimuladores();
  },
};
</script>
<style lang="css" scoped></style>
